<template>
  <div id="app">
    <Header
      :numCorrect = "numCorrect"
      :numTotal = "numTotal"
    />

    <quizCompleted
      v-if="allSubmitted"
    />

    <b-container class="bv-example-row">
      <b-row>
        <b-col sm="6" offset="3">
          <QuestionBox 
            v-if="questions.length && !allSubmitted"
            :currentQuestion = "questions[index]" 
            :skip = "skip"
            @next-question = "next"
            :increament = "increament" />
        </b-col>
      </b-row>
    </b-container>
    
  </div>
</template>

<script>
import Header from "./components/Header.vue"
import quizCompleted from "./components/quizCompleted.vue"
import QuestionBox from "./components/QuestionBox.vue"

export default {
  name: "app",
  components: {
    Header,
    quizCompleted,
    QuestionBox
  },
  data() {
    return {
      questions: [],
      index: 0,
      numCorrect: 0,
      numTotal: 0,
      allSubmitted: false
    }
  },
  watch: {
    numTotal() {
      if (this.numTotal == 10) {
        this.allSubmitted = true
      }
    }
  },
  methods: {
    next(num) {
      if (this.index == 9) this.allSubmitted = true
      else this.index += num
    },
    skip() {
      if (this.index == 9) this.allSubmitted = true
      else this.index += 1
    },
    increament(isCorrect) {
      if (isCorrect) {
        this.numCorrect++
      }
      this.numTotal++
    }
  },
  mounted: function() {
    fetch("https://opentdb.com/api.php?amount=10&category=18&type=multiple", {
      method: "get"
    })
      .then(response => {
        return response.json()
      })
      .then(jsonData => {
        this.questions = jsonData.results
      })
  }
}
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
