<template>
    <div class="question-box-container">
        <b-jumbotron>
            <template slot="lead">
                {{ domDecoder(currentQuestion.question) }}
            </template>

            <hr class="my-4">

            <b-list-group>
                <b-list-group-item 
                    v-for="(answer, index) in answers" :key="index"
                    @click="selectedAnswer(index)"
                    :class="[selectedindex === index ? 'selected' : '']"
                    >
                    {{ domDecoder(answer) }}
                </b-list-group-item>
            </b-list-group>

            <b-button 
                variant="primary" 
                @click="submitAnswer"
                :disabled="selectedindex === null || answered"
                >Submit</b-button>
            <b-button @click="skip" variant="success">Skip</b-button>
        </b-jumbotron>
    </div>
</template>

<script>
import _ from "lodash"

export default {
  props: {
    currentQuestion: Object,
    skip: Function,
    increament: Function
  },
  data() {
    return {
      selectedindex: null,
      correctIndex: null,
      shuffledAnswers: [],
      answered: false
    }
  },
  watch: {
    // currentQuestion() {
    //   this.selectedindex = null
    //   this.shuffleAnswers()
    // }

    currentQuestion: {
      immediate: true,
      handler() {
        this.answered = false
        this.selectedindex = null
        this.shuffleAnswers()
      }
    }
  },
  methods: {
    selectedAnswer(index) {
      this.selectedindex = index
    },
    submitAnswer() {
      let isCorrect = false

      if (this.selectedindex === this.correctIndex) {
        isCorrect = true
      }

      this.increament(isCorrect)
      this.answered = true

      this.$emit("next-question", 1)
    },
    shuffleAnswers() {
      let answers = [
        ...this.currentQuestion.incorrect_answers,
        this.currentQuestion.correct_answer
      ]

      this.shuffledAnswers = _.shuffle(answers)

      this.correctIndex = this.shuffledAnswers.indexOf(
        this.currentQuestion.correct_answer
      )
    },
    domDecoder(str) {
      let parser = new DOMParser()
      let dom = parser.parseFromString(
        "<!doctype html><body>" + str,
        "text/html"
      )
      return dom.body.textContent
    }
  },
  computed: {
    answers() {
      let answers = [...this.currentQuestion.incorrect_answers]
      answers.push(this.currentQuestion.correct_answer)
      return answers
    }
  }
}
</script>

<style scoped>
.list-group {
  margin-bottom: 15px;
  cursor: pointer;
}
.list-group-item:hover {
  background: #eee;
}
.btn {
  margin: 0 5px;
}
.selected {
  background-color: lightblue;
}
.correct {
  background-color: lightgreen;
}
.incorrect {
  background-color: red;
}
</style>